export default {
	props: {
		
		// 标题
		title: {
			type: String,
			default: ''
		},
		
		// 内容
		content: String,
		
		// 对齐方式
		textalign: {
			type: String,
			default: 'center'
		},
		
		// 取消文字
		cancelText: {
			type: String,
			default: '取消'
		},
		
		// 取消颜色
		cancelColor: {
			type: String,
			default: '#909399'
		},
		
		// 确定文字
		confirmText: {
			type: String,
			default: '确定'
		},
		
		// 确定文字颜色
		confirmColor: {
			type: String,
			default: '#409EFF'
		},
		
		// 是否显示取消按钮
		isShowCancel: {
			type: Boolean,
			default: true
		},
		
		// 是否显示弹出框
		status: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isShow: false
		}
	},
	watch: {
		show (val) {
			this.isShow = val
		}
	},
	methods: {
		// 禁止穿透
		bindTouchmove() {},
		
		show() {
			this.isShow = true;
		},
		
		// 取消方法
		clickCancel() {
			setTimeout(() => {
				this.$emit('cancelButton');
			}, 200);
			this.closeDialog();
		},
		
		// 确定方法
		clickConfirm() {
			setTimeout(() => {
				this.$emit('confirmButton');
			}, 200);
			this.closeDialog();
		},
		
		// 关闭弹窗
		closeDialog() {
			this.isShow = false;
			this.$emit('close')
		}
	}
}
